  width: 100%;
  margin-bottom: 47px;
  padding: 0 32px;
}
.header_art6__mobile-links > li {
  padding: 0;
  transition: background-color 0.2s ease-out;
}
.header_art6__mobile-links > li a,
.header_art6__mobile-links > li button {
  display: inline-block;
  width: 100%;
  font-size: 20px;
  font-weight: 600;
